.activity-item {
  min-height: 187px;
  background: #fff;
  border-radius: 12px;
  flex-direction: column;
  padding: 22px 26px 25px 27px;
  display: flex;
  box-shadow: 0 6px 10px #0000001a;
}

.activity-item-footer {
  margin-top: unset;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.activity-item-title {
  height: 100%;
  cursor: pointer;
  margin: 0 0 auto;
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: 27px;
}

.activity-item-dates {
  color: #888;
  margin: 0;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 21px;
}

.header {
  background: var(--primary);
  flex-direction: column;
  align-items: center;
  display: flex;
  box-shadow: 0 4px 10px #0000001a;
}

.header-background {
  width: 100%;
  height: 105px;
  line-height: 105px;
  top: 0;
  left: 0;
}

.header-title {
  text-transform: uppercase;
  color: #fff;
  vertical-align: middle;
  margin: 0;
  font-size: 24px;
  font-weight: 700;
  display: inline-block;
}

